.developer{
  display: flex;
  flex-wrap: wrap;
  align-items:center;

}
.developer__text{
  font-weight: 400;
  font-size: 14px;
  line-height: 26px;

}
.developer__link
{margin-left: 20px;}

.developer__link:hover svg path{
  fill:@logo-hover;
}
.developer__link:active svg path{
  fill:@logo-down;
}
